import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { TransactionInquiryComponent } from '../transaction-inquiry/transaction-inquiry.component';
import { FBTransactionsComponent } from './transactions.component';

const routes: Routes = [
  {
    path: '',
    component: FBTransactionsComponent
  },
  {
    path: 'transaction-inquiry',
    component: TransactionInquiryComponent
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class TransactionsRoutingModule { }
