import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatRippleModule } from '@angular/material/core';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyPaginatorModule as MatPaginatorModule } from '@angular/material/legacy-paginator';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { RouterModule } from '@angular/router';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, LoadingModule } from '@tei/material';
import { SecureNavigationModule } from '@tei/material-smart-components';
import { TransactionsRoutingModule } from './transactions-routing.module';
import { FBTransactionsComponent } from './transactions.component';
const COMPONENT = [FBTransactionsComponent];
const COMMON_MODULES = [CommonModule, FormsModule, ReactiveFormsModule, RouterModule];
const TEI_MATERIAL_MODULES = [
  LoadingModule,
  BreadCrumbModule,
  SecureNavigationModule,
];
const MATERIAL_MODULES = [MatTableModule, MatPaginatorModule, MatIconModule, MatCardModule, MatDividerModule, MatRippleModule, MatInputModule, MatFormFieldModule];
const CORE_MODULES = [TeiTextTranslatePipeModule];
@NgModule({
  declarations: [...COMPONENT],
  imports: [
    TransactionsRoutingModule,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES,
    ...COMMON_MODULES,
    ...MATERIAL_MODULES,
  ],
  exports: [
    ...COMPONENT
  ],
  providers: [
  ]
})
export class TransactionsModule { }
